.carousel {
  .carousel-inner {
    height: 240px;
    @media (min-width: 768px) {
      height: 400px; }
    @media (min-width: 975px) {
      height: 450px; }
    .head-logo {
      position: absolute;
      z-index: 111;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background: rgba(0,0,0,0.7);
      h1 {
        font-size: 4rem;
        color: #fff;
        letter-spacing: .5rem;
        @media(min-width: 975px) {
          font-size: 10rem; } }
      h2 {
        font-size: 2rem;
        color: #fff;
        letter-spacing: 1rem;
        @media(min-width: 975px) {
          font-size: 4rem;
          letter-spacing: 2rem; } } } } }
